import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    email: "",
    password: ""
  },
  getters: {
    getDetails: state => {
      return {
        email: state.email,
        password: state.password
      };
    }
  },
  mutations: {
    setEmail: (state, payload) => {
      state.email = payload
    },
    setPassword: (state, payload) => {
      state.password = payload
    }
  },
  actions: {},
  modules: {}
})